import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)


import Index from "../components/home/Index";
import Tab1 from "../components/home/Tab1";
import Tab2 from "../components/home/Tab2";
import Tab3 from "../components/home/Tab3";
import ManagerMain from "../components/manager/ManagerMain";
import Tab6 from "../components/home/Tab6";



const routes = [
  {
    path: '/',
    name: 'home',
    component: Index,
    children:[
      {
        path: '/first',
        name: 'first',
        component: Tab1,
      }, {
        path: '/second',
        name: 'second',
        component: Tab2,
      }, {
        path: '/third',
        name: 'third',
        component: Tab3,
      }, {
        path: '/seven',
        name: 'seven',
        component: ManagerMain,
      }, {
        path: '/six',
        name: 'six',
        component: Tab6,
      }
    ]
  }
]

export default new Router({
  routes,
})



