<template>
  <div class="main">
    <img v-bind:src="obj.url" class="image">
    <span style="margin-left:1rem">{{obj.text}}</span>
  </div>
</template>

<script>
  export default {
    name: "PicData",
    props: {
      obj: Object,
    },
    mounted() {
      console.log(this.obj.url)
    }
  }
</script>

<style lang="stylus" scoped>

  .main
    display flex;
    height 8rem;
    width 100%

    flex-direction row
    justify-content flex-start

  .image {

    width: auto;
    height: 100%;

  }
</style>



