<template>


  <div class="main">

    <div class="input_main">
      <el-input v-model="url" placeholder="请输入图片地址"/>
      <el-input v-model="text" placeholder="请输入图片描述" class="d_text"/>
    </div>
    <el-button @click="save_pic" style="width: 5rem;margin-top: 1rem">保存</el-button>
    <div v-for="(val ,key,index ) in pic_datas" class="list_main">
      <PicData :obj="val"/>
    </div>
  </div>


</template>

<script>
  import PicData from "./PicData";

  export default {
    name: "ManagerMain",
    components: {PicData},
    data() {
      return {
        pic_datas: [],
        url: '',
        text: '',
      }
    },
    methods: {
      save_pic(){
        if(this.url.length==0 || this.text.length==0){
          this.$message('请输入完整的数据');
          return
        }
        console.log(' save_pic '+this.url+  this.text)
        this.pic_datas.push({'url':this.url,text:this.text})
      }
    }
  }
</script>

<style lang="stylus" scoped>

  .main
    display flex;

    flex-direction column
    justify-content center
    align-items center

    .input_main
      display flex;
      width 80%
      .d_text
        margin-left: 1rem;
    .list_main

      width 80%
      margin-top 1rem


</style>



